.list-pointer-sm .list-pointer-item {
    padding-left: 1.5rem
}

.list-pointer-sm .list-pointer-item::before {
    width: 1rem;
    height: 1rem;
    background-size: 1rem 1rem;
    margin-top: .3125rem
}

.list-pointer-sm[class*=list-pointer-bg-] .list-pointer-item::before,
.list-pointer-sm[class*=list-pointer-soft-bg-] .list-pointer-item::before {
    margin-top: .25rem
}

.list-pointer-lg .list-pointer-item {
    padding-left: 2.75rem
}

.list-pointer-lg .list-pointer-item:not(:last-child) {
    margin-bottom: 1.25rem
}

.list-pointer-lg .list-pointer-item::before {
    width: 1.75rem;
    height: 1.75rem;
    background-size: 1.75rem 1.75rem;
    margin-top: 0
}

.list-pointer-lg[class*=list-pointer-bg-] .list-pointer-item::before,
.list-pointer-lg[class*=list-pointer-soft-bg-] .list-pointer-item::before {
    margin-top: -.0625rem
}

.modal-header {
    -ms-flex-align: center;
    align-items: center;
    padding-bottom: 0
}

.modal-header .close {
    padding: .25rem .25rem;
    margin: 0 0 0 auto
}

.modal-footer {
    border-top-width: .0625rem
}

.modal-footer>* {
    margin-top: 0;
    margin-bottom: 0
}

.modal-footer-text:last-child {
    font-size: .875rem;
    margin-bottom: 0
}

.modal-close {
    position: absolute;
    top: .75rem;
    right: .75rem;
    z-index: 2
}

.modal-top-cover {
    position: relative;
    overflow: hidden;
    min-height: 8rem;
    border-top-right-radius: .6875rem;
    border-top-left-radius: .6875rem
}

.modal-top-cover-avatar,
.modal-top-cover-icon {
    position: relative;
    z-index: 2;
    margin-top: -3rem
}

.nav-subtitle {
    display: block;
    color: #8c98a4;
    font-size: .65625rem;
    text-transform: uppercase;
    letter-spacing: .03125rem
}

.nav-link.dropdown-toggle {
    -ms-flex-align: center;
    align-items: center
}

.nav-link.active,
.navbar-nav .nav-link.active {
    color: #377dff
